@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@200;400;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;700&display=swap);
.app-container{text-align:center;width:1366px;height:768px;border-radius:15px 15px 15px 15px;box-shadow:0px 0px 6px 0px #807f7f;display:flex}.app-container .app-content-container{width:100%;height:100%;background-color:#fbcd69;border-radius:0 15px 15px 0}.app-container .page-title{color:#301b01;padding:20px}

.sidebar-button{width:100%;height:60px;letter-spacing:0.5px;line-height:50px;padding:0 35px 0 35px;font-size:15px;background-color:#1c1e22;color:#cf7500;text-transform:uppercase;border:none;cursor:pointer;display:flex;justify-content:center;align-items:center;font-family:"Noto Sans", sans-serif;font-weight:bold;margin-top:3px}.sidebar-button:hover{background-color:#202227;border-bottom:3px solid rgba(240,165,0,0.5)}.sidebar-button:active{transform:translateY(2px)}.sidebar-button:focus{outline:none}.sidebar-button.selected-button{background-color:#202227;border-bottom:4px solid #f0a400}

.sidebar-container{background-color:#1a1c20;height:100%;width:300px;border-radius:15px 0 0 15px;display:flex;flex-direction:column;justify-content:space-between}.sidebar-container .sidebar-header{width:300px;height:200px;display:flex;flex-direction:row;background-color:#202227;border-radius:15px 0 0 0;border-bottom:5px solid #15171a;overflow:hidden}.sidebar-container .sidebar-header .nodemcu-image{background-image:url(/nodemcu-home-project/static/media/esp8266_nodemcu.35237f25.png);width:40%;height:200px;background-size:80px;margin-top:30px;margin-left:10px;background-repeat:no-repeat;transform:rotate(20deg)}.sidebar-container .sidebar-header .nodemcu-title{color:#cf7500;font-size:30px;display:inline-flex;align-items:center}.sidebar-container .sidebar-links-container{width:100%;height:90px;border-radius:0 0 0 15px;background-color:#202227;border-top:5px solid #15171a;display:inline-flex;justify-content:center;align-items:center}.sidebar-container .sidebar-links-container .github-link{cursor:pointer;width:40px;height:40px;background-image:url(/nodemcu-home-project/static/media/github.fa0212fe.svg);background-size:cover;-webkit-filter:invert(45%) sepia(59%) saturate(968%) hue-rotate(2deg) brightness(95%) contrast(103%);filter:invert(45%) sepia(59%) saturate(968%) hue-rotate(2deg) brightness(95%) contrast(103%)}.sidebar-container .sidebar-links-container .github-link:hover{-webkit-filter:invert(38%) sepia(76%) saturate(5204%) hue-rotate(34deg) brightness(80%) contrast(98%);filter:invert(38%) sepia(76%) saturate(5204%) hue-rotate(34deg) brightness(80%) contrast(98%)}

.homepage-container{width:100%;height:100%;display:flex;align-items:center;flex-direction:column}.homepage-container .homepage-text{background-color:#ffdf99;padding:50px;margin-top:50px;width:60%;display:flex;flex-direction:column;justify-content:space-around;text-align:justify}.homepage-container .homepage-text::before{position:absolute;margin-left:200px;transform:rotate(30deg);width:250px;height:450px;content:"";background-image:url("https://firebasestorage.googleapis.com/v0/b/ev-otomasyon-f0c18.appspot.com/o/assets%2Fesp8266_nodemcu.png?alt=media&token=b8205306-842a-4599-bc10-191ac7045293");background-size:cover;opacity:0.1}.homepage-container .homepage-text .first-h2{text-align:right}

.chart-infos{width:100%;height:100%;display:flex;flex-direction:column}.chart-infos .info-header{background-color:#f3efef;height:60px;display:flex;align-items:center;justify-content:space-evenly}.chart-infos .info-header .latest-value{font-size:20px}.chart-infos .info-chart{height:100%;width:100%;display:flex;justify-content:center;align-items:center}

.chart-button-container{border:none;width:140px;height:180px;background-color:#eeeff2;cursor:pointer}.chart-button-container:hover{background-color:#dddee1}.chart-button-container:focus{outline:none}.chart-button-container:active{transform:translateY(1px)}.chart-button-container.selected-button{border-right:3px solid #f06000;background-color:#dddee1}.chart-button-container .button-icon{width:60px;height:60px;background-size:cover;margin:auto}.chart-button-container .button-title{font-weight:normal;margin-top:auto}

.charts-content-container{display:flex;flex-direction:column;width:100%;height:100%;align-items:center}.charts-content-container .charts-container{width:840px;height:480px;margin-top:50px;background-color:white;border-radius:10px;display:flex;overflow:hidden;box-shadow:0px 0px 6px 0px #6b6b6b}.charts-content-container .charts-container .chart-buttons-container{width:140px;height:100%;border-radius:10px 0 0 10px;overflow:hidden;display:flex;flex-direction:column}.charts-content-container .charts-container .chart-info-container{width:700px}

.control-button-container{width:300px;height:300px;background-color:white;border-radius:10px;box-shadow:0px 0px 6px 0px #807f7f;border:none;transition-duration:0.1s;cursor:pointer;display:flex;flex-direction:column;align-items:center;justify-content:space-between;overflow:hidden;padding:0}.control-button-container:focus{outline:none}.control-button-container:active{transform:translateY(3px)}.control-button-container .button-icon{width:120px;height:120px;background-size:cover}.control-button-container .status-container{width:300px;display:inline}.control-button-container .status-container.status-open{background-color:#bcd4b5;border-top:2px solid #97b98e}.control-button-container .status-container.status-closed{background-color:#f5c7c7;border-top:2px solid #d4a1a1}

.info-box-container{width:210px;height:60px;background-color:white;border-radius:5px 5px 0 0;box-shadow:0px 3px 1px 0px gray;display:flex;justify-content:space-evenly;align-items:center;cursor:default}.info-box-container:hover{background-color:#e7e7e7}.info-box-container .info-value{font-size:35px;font-weight:bold}.info-box-container .info-value.low{color:blue}.info-box-container .info-value.mid{color:green}.info-box-container .info-value.max{color:red}.info-box-container .info-value .info-unit{font-size:20px}.info-box-container .info-name{font-size:15px;font-weight:bold;height:10%}

.controls-content-container{display:flex;flex-direction:column;width:100%;height:100%;align-items:center}.controls-content-container .controls-container{margin-top:50px;width:70%;height:50%;display:flex;justify-content:space-between;align-items:center}.controls-content-container .info-container{width:70%;display:flex;justify-content:space-between;align-items:center}

body {
  margin: 0;
  height: 100vh;
  background-color: rgb(251, 243, 238);
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Noto Sans", sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

